<template>
  <div>
    <div :style="{height: specHeight + 'px'}" style="width: 100%;"></div>
    <mt-header style="padding: 0px;" title="账号信息">
      <router-link to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div style="width: 100%; text-align: center; height: 160px; background: #00a0ea">
      <img style="margin-top: 20px;" src="static/img/avatar.png" width="64px">
      <br/>
      <span style="font-size: 20px; color: #ffffff">{{username}}</span>
    </div>
    <mt-cell title="版本号" value="V1.2.0"></mt-cell>
    <mt-cell @click.native="loginOut()" title="退出登陆" is-link></mt-cell>
  </div>
</template>
<script>

export default {
  data () {
    return {
      tableData: [],
      popupVisible: false,
      sheetVisible: false,
      username: this.$store.getters.name + '(' + this.$store.getters.userno + ')',
      projname: '',
      specHeight: this.$store.getters.statusBarHeight,
      actions: [{
        name: '大亚湾项目',
        method: this.clickHandle
      },
      {
        name: '大亚湾项目2',
        method: this.clickHandle
      }]

    }
  },
  methods: {
    loginOut () {
      this.$store.dispatch('LogOut').then(() => {
        console.log('login--------------')
        location.reload()// In order to re-instantiate the vue-router object to avoid bugs
      })
    },
    clickHandle () {
      console.log('11111111111111')
      this.sheetVisible = !this.sheetVisible
      this.projname = '大亚湾项目2'
    }
  }
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
</style>



